.ui-widget-content {
  color: #8B9AA7;
  border-color: #f1f2f4; }

.ui-widget.ui-widget-content, .ui-timepicker-table.ui-widget-content {
  background: #fff;
  box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.5); }

.ui-widget {
  font-family: inherit;
  font-size: inherit; }

.ui-widget-header {
  border: 0;
  background: #E3ECF2;
  color: #8B9AA7;
  font-weight: bold; }
  .ui-widget-header a {
    color: #8B9AA7; }

.ui-state-default, .ui-widget-content .ui-state-default, .ui-widget-header .ui-state-default {
  border: 1px solid #EDEDED;
  background: #fff;
  font-weight: bold;
  color: #8B9AA7;
  border-radius: 3px; }

.ui-widget-header .ui-state-default {
  background: none;
  color: #8B9AA7;
  border: 0; }

.ui-state-hover, .ui-widget-content .ui-state-hover, .ui-widget-header .ui-state-hover, .ui-state-focus, .ui-widget-content .ui-state-focus, .ui-widget-header .ui-state-focus {
  border: 1px solid #1cacfc;
  background: #1cacfc;
  font-weight: bold;
  color: #fff; }

.ui-state-active, .ui-widget-content .ui-state-active, .ui-widget-header .ui-state-active {
  border: 1px solid #A464C4;
  background: #A464C4;
  font-weight: bold;
  color: #fff; }

.ui-state-highlight, .ui-widget-content .ui-state-highlight, .ui-widget-header .ui-state-highlight {
  border: 1px solid #1cacfc;
  background: #fff;
  color: #1cacfc; }

@media only screen and (max-width: 480px) {
  .ui-dialog {
    left: 10px !important;
    right: 10px !important;
    width: auto !important; } }

.ui-dialog-buttonpane {
  background: #E3ECF2;
  margin: .5em -0.2em -0.2em -0.2em; }
  .ui-dialog-buttonpane .ui-button {
    border: 0 !important;
    outline: 0; }

.ui-icon {
  font-family: 'jet-icons';
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  display: inline-block;
  font-size: 16px;
  font-weight: bold;
  background: none !important;
  text-indent: 0;
  overflow: visible; }

.ui-icon-circle-triangle-e:before {
  content: ""; }

.ui-icon-circle-triangle-w:before {
  content: ""; }

.ui-icon-closethick:before {
  content: ""; }

.ui-widget-overlay {
  background: #000;
  opacity: 0.5;
  filter: Alpha(Opacity=50); }

.ui-tooltip {
  background: #000 !important;
  color: #fff;
  border: 0;
  box-shadow: none !important;
  opacity: 0.8;
  font-size: 13px;
  pointer-events: none; }

.ui-datepicker table, .ui-timepicker table {
  margin: 0 0 .4em;
  background: transparent;
  border-radius: 0;
  box-shadow: none; }

.ui-datepicker th, .ui-timepicker th {
  background: inherit;
  color: inherit;
  text-transform: inherit; }

.ui-datepicker tbody tr, .ui-timepicker tbody tr {
  border-bottom: inherit; }

.ui-datepicker table {
  margin: 0 0 .4em; }

.ui-timepicker-table table {
  margin: .15em 0 0; }
